import React from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Card, CardBody, CardTitle, Form, FormGroup, Label, Col, Input, FormText } from 'reactstrap';

class AddInvoiceModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
      nestedModal: false,
      closeAll: false
    };

    this.toggle = this.toggle.bind(this);
    this.toggleNested = this.toggleNested.bind(this);
    this.toggleAll = this.toggleAll.bind(this);
  }

  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }

  toggleNested() {
    this.setState({
      nestedModal: !this.state.nestedModal,
      closeAll: false
    });
  }

  toggleAll() {
    this.setState({
      nestedModal: !this.state.nestedModal,
      closeAll: true
    });
  }

  render() {
    return (
      <span className="d-inline-block mb-2 me-2">
        {/* <Button color="primary" onClick={this.toggle}>Nested Modal</Button> */}
        <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
          {/* <ModalHeader toggle={this.toggle}>Add Invoice</ModalHeader> */}
          <ModalBody>
            <Card className="main-card mb-3">
              <CardBody>
                {/* <CardTitle>Grid</CardTitle> */}
                <Form>

                  <FormGroup row>
                    <Label for="gr" sm={10}>GR #</Label>
                    <Col sm={12}>
                      <Input type="text" name="gr" id="gr"
                        placeholder="Enter GR #" />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label for="invoice" sm={10}>Invoice Number</Label>
                    <Col sm={12}>
                      <Input type="text" name="invoice" id="invoice"
                        placeholder="Enter Invoice Number" />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label for="date" sm={10}>Invoice Date</Label>
                    <Col sm={12}>
                      <Input type="text" name="date" id="date"
                        placeholder="Enter Invoice Date" />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label for="amount" sm={10}>Invoice Amount</Label>
                    <Col sm={12}>
                      <Input type="text" name="amount" id="amount"
                        placeholder="Enter Invoice Amount" />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label for="name" sm={10}>Client Name</Label>
                    <Col sm={12}>
                      <Input type="text" name="name" id="name"
                        placeholder="Enter Client Name" />
                    </Col>
                  </FormGroup>

                  {/* <FormGroup row>
                                        <Label for="exampleSelect" sm={2}>Select</Label>
                                        <Col sm={10}>
                                            <Input type="select" name="select" id="exampleSelect">
                                            <option value="">Select</option>
                                                    <option>Value 1</option>
                                                    <option>Value 2</option>
                                                    <option>Value 3</option>
                                                    <option>Value 4</option>
                                                    <option>Value 5</option>
                                                    </Input>
                                        </Col>
                                    </FormGroup> */}

                  <FormGroup col>
                    <Label for="comment" sm={10}>Comments (If any)</Label>
                    <Col sm={12}>
                      <Input type="textarea" name="comment" id="comment" placeholder="Enter Comments" />
                    </Col>
                  </FormGroup>
                </Form>
              </CardBody>
            </Card>
            {/* <br /><br /> */}
            {/* <Button color="success" onClick={this.toggleNested}>Show Nested Modal</Button> */}
            <Modal isOpen={this.state.nestedModal} toggle={this.toggleNested} onClosed={this.state.closeAll ? this.toggle : undefined}>
              <ModalHeader>Invoice Summary</ModalHeader>
              <ModalBody>
                <Form>
                  <FormGroup row>
                    <Label sm={5}>GR # :</Label>
                    <Label sm={5}>12345</Label>
                  </FormGroup>
                  <FormGroup row>
                    <Label sm={5}>Invoice Number :</Label>
                    <Label sm={5}>123456</Label>
                  </FormGroup>
                  <FormGroup row>
                    <Label sm={5}>Invoice Date :</Label>
                    <Label sm={5}>28/04/2023</Label>
                  </FormGroup>
                  <FormGroup row>
                    <Label sm={5}>Invoice Amount :</Label>
                    <Label sm={5}>500</Label>
                  </FormGroup>
                  <FormGroup row>
                    <Label sm={5}>Client Name :</Label>
                    <Label sm={5}>John Doe</Label>
                  </FormGroup>
                  <FormGroup row>
                    <Label sm={5}>Comments (If any) :</Label>
                    <Label sm={5}>No Comments</Label>
                  </FormGroup>
                </Form>
              </ModalBody>
              <ModalFooter>
                <Button color="link" onClick={this.toggleNested}>Cancel</Button>
                <Button color="primary" onClick={this.toggleAll}>Confirm</Button>{' '}
              </ModalFooter>
            </Modal>
          </ModalBody>
          <ModalFooter>
            <Button color="link" onClick={this.toggle}>Cancel</Button>
            <Button color="primary" onClick={this.toggleNested}>Done</Button>{' '}
          </ModalFooter>
        </Modal>
      </span>
    );
  }
}

export default AddInvoiceModal;
